import React, { useState } from 'react';
import style from './PagesLayout.module.css';

const PagesLayout = ({ children, setPageKey }) => {
  return (
    <div className={style.container}>
      <div className={style.body}>{children}</div>
      <div className={style.sideBarRight}>
        <div className={style.menuItem}>
          <h1 className={style.titleMenuItem}>Trang chủ</h1>
          <ul className={style.menuList}>
            {ARRAY_MENU_HOME.map((m, i) => (
              <li
                key={i}
                className={style.subMenuItem}
                onClick={() => setPageKey(m)}
              >
                {m.name}
              </li>
            ))}
          </ul>
        </div>
        <div className={style.menuItem}>
          <h1 className={style.titleMenuItem}>Giới thiệu</h1>
          <ul className={style.menuList}>
            {ARRAY_MENU_INTRO.map((m, i) => (
              <li
                key={i}
                className={style.subMenuItem}
                onClick={() => setPageKey(m)}
              >
                {m.name}
              </li>
            ))}
          </ul>
        </div>
        <div className={style.menuItem}>
          <h1 className={style.titleMenuItem}>Dịch vụ</h1>
          <ul className={style.menuList}>
            {ARRAY_MENU_SERVICES.map((m, i) => (
              <li
                key={i}
                className={style.subMenuItem}
                onClick={() => setPageKey(m)}
              >
                {m.name}
              </li>
            ))}
          </ul>
        </div>
        <div className={style.menuItem}>
          <h1 className={style.titleMenuItem}>Hỗ trợ</h1>
          <ul className={style.menuList}>
            {ARRAY_MENU_SUPPORT.map((m, i) => (
              <li
                key={i}
                className={style.subMenuItem}
                onClick={() => setPageKey(m)}
              >
                {m.name}
              </li>
            ))}
          </ul>
        </div>
        <div className={style.menuItem}>
          <h1 className={style.titleMenuItem}>Tin tức</h1>
          <ul className={style.menuList}>
            {ARRAY_MENU_NEWS.map((m, i) => (
              <li
                key={i}
                className={style.subMenuItem}
                onClick={() => setPageKey(m)}
              >
                {m.name}
              </li>
            ))}
          </ul>
        </div>
      </div>
    </div>
  );
};

export default PagesLayout;
const ARRAY_MENU_INTRO = [
  { pageKey: 'letter', name: 'Thư ngỏ' },
  { pageKey: 'history', name: 'Lịch sử phát triển' },
  { pageKey: 'overview', name: 'Tổng quan công ty' },
  { pageKey: 'outstanding', name: 'Ưu điểm vượt trội' },
  { pageKey: 'partner', name: 'Đối tác và khách hàng' },
  { pageKey: 'achievements', name: 'Thành tích đạt được' },
  { pageKey: 'profile', name: 'Hồ sơ năng lực' },
];
const ARRAY_MENU_HOME = [{ pageKey: 'home', name: 'Home' }];
const ARRAY_MENU_SERVICES = [
  { pageKey: 'services1', name: 'Vận tải đường bộ' },
  { pageKey: 'services2', name: 'Vận tải đường sắt' },
  { pageKey: 'services3', name: 'Vận tải đường biển' },
  { pageKey: 'services4', name: 'Vận tải đường hàng không' },
  { pageKey: 'services5', name: 'Thủ tục hải quan' },
  { pageKey: 'services6', name: 'Kho bãi và lưu trữ hàng hóa' },
  { pageKey: 'services7', name: 'Đóng gói và chia chọn' },
  { pageKey: 'services8', name: 'Giao hàng điểm cuối' },
];
const ARRAY_MENU_SUPPORT = [
  { pageKey: 'question', name: 'Câu hỏi thường gặp' },
  { pageKey: 'comments', name: 'Gợi ý sản phẩm dịch vụ' },
  { pageKey: 'search', name: 'Tra cứu' },
  { pageKey: 'cooperate', name: 'Hợp tác' },
];
const ARRAY_MENU_NEWS = [
  { pageKey: 'news', name: 'Tin tức' },
  { pageKey: 'newsdetail', name: 'Tin chi tiết' },
];
