import React, { useRef, useEffect } from 'react';
import PropTypes from 'prop-types';
import clsx from 'clsx';
import Messenger from './messenger.svg';

import {
  Header,
  Footer,
  FOOTER_HEIGHT,
  HomeFooter,
  HOME_FOOTER_HEIGHT,
} from './components';

const HomeLayout = ({ children, typeFooter }) => {
  const linkRef = useRef();
  const spanRef = useRef();
  useEffect(() => {
    if (window.location.pathname === '/') {
      spanRef.current.classList.add('home');
      linkRef.current.classList.add('home');
    }
  }, [window.location.pathname]);
  return (
    <>
      <div>
        <Header />
        <main
          className={clsx(
            typeFooter === 'home' ? 'home-body' : 'body',
            'fullwidth'
          )}
        >
          {children}
        </main>
        {typeFooter ? FOOTER_OPTION[typeFooter].components : <Footer />}
        <a
          href="https://m.me/275542932584182"
          className="messenger"
          target="_blank"
          rel="nofollow noopener noreferrer"
          ref={linkRef}
        >
          <img
            src={Messenger}
            width="48"
            height="48"
            title="Tư vấn trên Facebook Messenger"
            alt="Tư vấn trên Facebook Messenger"
          />
        </a>
        <span ref={spanRef} className="messengerText">
          Hotline: 18008095 Email: cskh@viettellogistics.com.vn
        </span>
      </div>
    </>
  );
};

HomeLayout.propTypes = {
  children: PropTypes.node,
  typeFooter: PropTypes.string,
};

export default HomeLayout;

const FOOTER_OPTION = {
  home: {
    height: HOME_FOOTER_HEIGHT,
    components: <HomeFooter />,
  },
  copyright: {
    height: FOOTER_HEIGHT,
    components: <Footer />,
  },
};
